/* css reset */
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  min-height: 100%;
  position: relative;
  background: #f3f3f3 !important;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial,
    sans-serif;
  font-size: 16px;
}

.App-header {
  background: #fff;
  border-bottom: 1px solid #e8e8e8;
  position: relative;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.05);
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  padding-left: 21%;
  color: #1da1f2;
}

.posts-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.posts-list :hover {
  background: #f3f3f3;
}

.post-item,
.modal-content {
  overflow: hidden;
  position: relative;
  background: white;
  margin: 5px 0;
  box-shadow: 0 0 15px lightgrey;
  padding: 10px 10px 0 10px;
  cursor: pointer;
  display: block !important;
}

.thumbnail {
  margin-right: 10px;
  margin-bottom: 10px;
  position: relative;
  float: left;
}
.thumbnail img {
  height: 100px;
  width: 80px;
}

.content {
  padding: 4px 0px 0 0;
  font-size: 16px;
  font-weight: 600;
}
.tag-line {
  font-size: 14px;
  color: rgb(153, 153, 153);
}

.like-button {
  position: absolute;
  top: 50%;
  right: 20px;
}

.meta_info {
  font-size: 14px;
  color: #2d89a7;
  border: 1px solid #e8e8e8;
  padding: 2px 2px;
}

.comments {
  position: relative;
  float: left;
  width: 100%;
}

.date-pick {
  margin: 10px 0px;
}
.date-pick-text {
  font-size: 16px;
}
.comments-label {
  color: rgb(153, 153, 153);
  font-size: 16px;
  float: left;
}

.comment-wrapper {
  list-style: none;
  box-shadow: 0 0 2px gray;
  padding: 5px 5px 3px 5px;
  margin: 5px 0;
}
.comment-image {
  margin-right: 10px;
  position: relative;
  float: left;
}
.comment-image img {
  border-radius: 50%;
}
.load-button {
  width: 100%;
  float: left;
  margin-bottom: 10px;
}

.fav-label {
  font-size: 18px;
  margin: 0;
  font-weight: bold;
  padding: 9px 0;
}
.fav-thumbnail {
  margin-right: 10px;
  position: relative;
  float: left;
  margin-bottom: 10px;
}

.fav-thumbnail img {
  height: 99px;
  width: 80px;
}

.right {
  float: right;
}

.left {
  float: left;
}

.full-width {
  width: 100%;
}

.comment-list {
  margin-top: 30px;
}

